
@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap');

$color-main: #FFF9F6;
$blue-highlight: #96D1DC;
$green-highlight: #B4DBBB;
$tan-highlight: #EFBBA4;
$dark-highlight: #043340;
$title-font: 'Bebas Neue', cursive;


body {
  margin: 0 auto;
  text-align: center;
}

h1 span {
  position: relative;
  top: 15px;
  font-size: 65px;
  animation: bounce 2s ease infinite alternate;
  text-shadow: 0 1px $tan-highlight,
  0 2px $tan-highlight,
  0 3px $tan-highlight,
  0 4px $tan-highlight,
  0 5px $tan-highlight,
  0 6px 0 transparent,
  0 7px 0 transparent,
  0 8px 0 transparent,
  0 9px 0 transparent,
  0 10px 10px 0 rgba(0,0,0,.4);
}

.site-title{
  span:nth-child(2) {
    animation-delay: .1s;
  }
  span:nth-child(3) {
    animation-delay: .3s;
  }
  span:nth-child(4) {
    animation-delay: .5s;
  }
  span:nth-child(5) {
    animation-delay: .1s;
  }
  span:nth-child(6) {
    animation-delay: .3s;
  }
  span:nth-child(7) {
    animation-delay: .5s;
  }
  span:nth-child(8) {
    animation-delay: .7s;
  }
}


.logo {
  width: 240px;
}

.navbar ul {
  display: inline;
  margin: 0px;
  padding: 0px;
}

ul li {
  list-style: none;
  display: inline;
  padding: 10px;
  font-size: 25px;
}

.grid-container.navbar {
  width: 100vw;
  display: inline-grid;
  grid-template-columns: 1.3fr 2.7fr 1fr;
  height: 125px;
  background-color: $dark-highlight;
}

.nav1 {
  align-self: center;
}
.nav2 {
  align-self: center;
}

.nav3 {
  align-self: center;
}

.Modal {
  width: 350px;
  height: 50vh;
  margin: 55px auto;
  border: 3px solid $dark-highlight;
  border-radius: 25px;
  background-color: $color-main;
}

.Overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #EFBBA49C;
}

a {
  text-decoration: none;
  color: $color-main;
  font-family: $title-font;
}

a:hover {
  cursor: pointer;
  color: $blue-highlight;
}

button {
  transition-duration: 0.3s;
  background-color: $color-main;
  border: 1px solid $dark-highlight;
  color: $dark-highlight;
  font-size: 20px;
  font-family: $title-font;
  font-weight: bold;
  width: 110px;
  height: 40px;
  border-radius: 25px;
  margin: 5px;
}

button:hover {
  background-color: $dark-highlight;
  border: 1px solid $color-main;
  color: $color-main;
  width: 120px;
  height: 40px;
}

.modal-close {
  background-color: #EFBBA400;
  color: $dark-highlight;
  font-size: 20px;
  position: relative;
  right: -40%;
  border: none;
}

.modal-close:hover {
  background-color: #EFBBA400;
  font-size: 25px;
  border: none;
}

.modal-link {
  color: $dark-highlight;
  font-size: 20px;
}

.input-item {
  margin: 10px auto;
  height: 33px;
  display: block;
  font-size: 16px;
  padding: 0.25em 0.5em;
  background-color: $color-main;
  border: 1px solid $dark-highlight;
  box-shadow: 4px 2px $dark-highlight;
  border-radius: 15px;
}

.input-item:focus {
  outline: none;
  background-color: #B4DBBB73;
  height: 40px;
}

.hide {
  display: none;
}

.home.grid-container {
  display: grid;
  grid-template-columns: 1fr 2fr 3fr;
  margin-top: 15px;
  column-gap: 30px;
}



.newsletter-container {
  background-color: $dark-highlight;
  border-radius: 15px;
  margin: 0 auto;
  padding: 20px;

  p {
    color: $color-main;
  }
}

