section {
position: relative;
width: 100%;
background-color: $color-main;
overflow: hidden;

  .wave {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100px;
    background-image: url('./wave.png');
    background-size: 1000px 100px;
  }
  .wave1 {
    animation: animate 30s linear infinite;
    z-index: 1000;
    opacity: 1;
    animation-delay: 0s;
    bottom: 0;
  }
  .wave2 {
    animation: animate2 15s linear infinite;
    z-index: 999;
    opacity: 0.5;
    animation-delay: -5s;
    bottom: 10px;
  }
  .wave3 {
    animation: animate 30s linear infinite;
    z-index: 998;
    opacity: 0.2;
    animation-delay: -2s;
    bottom: 15;
  }
  .wave4 {
    animation: animate2 5s linear infinite;
    z-index: 997;
    opacity: 0.7;
    animation-delay: -5s;
    bottom: 20px;
  }
}

.wave-container {
  height: 50px;
  margin-top: 100px;
}

@keyframes animate {
  0%
  {
    background-position-x: 0;
  }
  100%
  {
    background-position-x: 1000px;
  }
}
@keyframes animate2 {
  0%
  {
    background-position-x: 0;
  }
  100%
  {
    background-position-x: -1000px;
  }
}

@keyframes bounce {
  100% {
    top: 0px;
    text-shadow: 0 1px 0 $green-highlight,
    0 2px 0 $green-highlight,
    0 3px 0 $green-highlight,
    0 4px 0 $green-highlight,
    0 5px 0 $green-highlight,
    0 6px 0 $green-highlight,
    0 7px 0 $green-highlight,
    0 8px 0 $green-highlight,
    0 9px 0 $green-highlight,
    0 50px 25px rgba(0,0,0,.2)
  }
}
