:#043340;font-size:20px;position:relative;right:-40%;border:none}.modal-close:hover{background-color:#EFBBA400;font-size:25px;border:none}.modal-link{color:#043340;font-size:20px}.input-item{margin:10px auto;height:33px;display:block;font-size:16px;padding:0.25em 0.5em;background-color:#FFF9F6;border:1px solid #043340;box-shadow:4px 2px #043340;border-radius:15px}.input-item:focus{outline:none;background-color:#B4DBBB73;height:40px}.hide{display:none}.home.grid-container{display:grid;grid-template-columns:1fr 2fr 3fr;margin-top:15px;grid-column-gap:30px;-webkit-column-gap:30px;column-gap:30px}.newsletter-container{background-color:#043340;border-radius:15px;margin:0 auto;padding:20px}.newsletter-container p{color:#FFF9F6}section{position:relative;width:100%;background-color:#FFF9F6;overflow:hidden}section .wave{position:absolute;bottom:0;left:0;width:100%;height:100px;background-image:url(data:image/png;base64,);background-size:1000px 100px}section .wave1{-webkit-animation:animate 30s linear infinite;animation:animate 30s linear infinite;z-index:1000;opacity:1;-webkit-animation-delay:0s;animation-delay:0s;bottom:0}section .wave2{-webkit-animation:animate2 15s linear infinite;animation:animate2 15s linear infinite;z-index:999;opacity:0.5;-webkit-animation-delay:-5s;animation-delay:-5s;bottom:10px}section .wave3{-webkit-animation:animate 30s linear infinite;animation:animate 30s linear infinite;z-index:998;opacity:0.2;-webkit-animation-delay:-2s;animation-delay:-2s;bottom:15}section .wave4{-webkit-animation:animate2 5s linear infinite;animation:animate2 5s linear infinite;z-index:997;opacity:0.7;-webkit-animation-delay:-5s;animation-delay:-5s;bottom:20px}.wave-container{height:50px;margin-top:100px}@-webkit-keyframes animate{0%{background-position-x:0}100%{background-position-x:1000px}}@keyframes animate{0%{background-position-x:0}100%{background-position-x:1000px}}@-webkit-keyframes animate2{0%{background-position-x:0}100%{background-position-x:-1000px}}@keyframes an